<template>
    <transition appear name="main-fadein">
        <div class="delete-card" ref="card">
            <div class="text-container">
                <h2 class="delete-headline">{{ DELETE_MAIN }}</h2>
                <p class="delete-subtext">{{ DELETE_SUB }}</p>
            </div>
            <div class="button-container">
                <button class="cancel-button" @click="cancel">{{ CANCEL }}</button>
                <button class="delete-button" @click="confirm">
                    {{ DELETE_ALL }}
                </button>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    name: 'DeleteCard',
    data: function () {
        return {
            DELETE_MAIN: '',
            DELETE_SUB: '',
            DELETE_ALL: '',
            CANCEL: '',
        };
    },
    props: {
        expanded: Boolean,
    },
    mounted() {
        this.fetchStrings();
    },
    methods: {
        cancel: function () {
            this.$parent.$emit('toggle_delete_card', {
                toggle: false,
                expandedState: false,
            });
        },
        confirm: function () {
            this.$root.$emit('clear_library');
        },
        fetchStrings: function () {
            this.DELETE_MAIN = browser.i18n.getMessage('delete_main');
            this.DELETE_SUB = browser.i18n.getMessage('delete_sub');
            this.DELETE_ALL = browser.i18n.getMessage('delete_all');
            this.CANCEL = browser.i18n.getMessage('button_cancel');
        },
    },
};
</script>

