<template>
    <div class="no-match-card">
        <transition appear name="main-fadein">
            <svg id="icon-exclamation" viewBox="0 0 120 120">
                <title>Path</title>
                <path
                    id="prefix__a"
                    d="M60 0c33.137 0 60 26.863 60 60s-26.863 60-60 60S0 93.137 0 60 26.863 0 60 0zm0 83.077c-4.076 0-7.385 3.306-7.385 7.385 0 4.076 3.309 7.384 7.386 7.384s7.384-3.308 7.384-7.384c0-4.079-3.307-7.385-7.384-7.385zm-5.32-59.638c-4.342 2.24-7.315 7.163-7.585 12.585-.152 3.03.699 5.885 1.497 8.73.612 2.374 1.227 4.761 1.9 7.117 1.217 4.734 2.421 9.509 3.78 14.19.612 2.409 1.019 5.316 2.96 6.83 2.089 1.623 5.001 1.128 6.594-1.106.429-.6.715-1.278.92-2.006l.076-.287.007-.027.004-.016.002-.007.001-.003-.006.026c1.366-4.68 2.54-9.458 3.776-14.184 1.343-4.743 2.612-9.539 3.823-14.33 1.42-5.627-.286-11.809-4.263-15.505-3.754-3.489-9.066-4.283-13.486-2.007z"
                />
            </svg>
        </transition>

        <transition appear name="main-fadein">
            <div class="no-match-text">
                <p class="title-text" v-if="wasSilent"> {{ NO_AUDIO_MAIN }}</p>
                <p class="title-text" v-else>{{ NO_RESULT_MAIN }}</p>
                
                <p class="sub-text" v-if="wasSilent">{{ NO_AUDIO_SUB }}</p>
                <p class="sub-text" v-else>{{ NO_RESULT_SUB }}</p>
            </div>
        </transition>
        <transition appear name="main-fadein">
            <div class="button-container" ref='buttonContainer'>
                <button @click="back" class="back-button">
                    {{ CANCEL }}
                </button>
                <button @click="tryCaptureAgain" class="try-again-button">
                    {{ TRY_AGAIN }}
                </button>
            </div>
        </transition>
    </div>
</template>

<script>
export default {
    name: 'NoMatchCard',
    data: function () {
        return {
            NO_RESULT_MAIN: '',
            NO_RESULT_SUB: '',
            NO_AUDIO_MAIN: '',
            NO_AUDIO_SUB: '',
            TRY_AGAIN: '',
            CANCEL: '',
        };
    },
    props: {
        wasSilent: Boolean,
    },
    mounted() {
        this.fetchStrings();

        const noMatchBeaconObject = {
            beaconType: 'pageview',
            screenname: !this.wasSilent ? 'nomatch' : 'nomatch_noaudio',
        };
        this.sendBeacon(noMatchBeaconObject);

        this.$root.$on('library_toggled', () => {
            // this.$refs.buttonContainer.classList.toggle();
        });
    },
    methods: {
        tryCaptureAgain: function () {
            this.$parent.$emit('close_match_restart');

            const tryAgainUserevent = {
                beaconType: 'userevent',
                type: 'taggingstarted',
                screnname: !this.wasSilent ? 'nomatch' : 'nomatch_audio',
            };

            this.sendBeacon(tryAgainUserevent);
        },
        back: function () {
            this.$parent.$emit('close_match');
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        fetchStrings: function () {
            this.NO_RESULT_MAIN = browser.i18n.getMessage('nomatch_couldnt_catch');
            this.NO_RESULT_SUB = browser.i18n.getMessage('nomatch_please_try_again');
            this.NO_AUDIO_MAIN = browser.i18n.getMessage('nomatch_no_audio_main');
            this.NO_AUDIO_SUB = browser.i18n.getMessage('nomatch_no_audio_sub');
            this.TRY_AGAIN = browser.i18n.getMessage('nomatch_try_again');
            this.CANCEL = browser.i18n.getMessage('button_cancel');
        },
    },
};
</script>

