import beaconsManifest from './beacons.js';
import Config from '../config.js';

function guid() {
    function s4() {
        return Math.floor((1 + Math.random()) * 0x10000)
            .toString(16)
            .substring(1);
    }
    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
}

const MOCKACTIVATE = false;
const origin = Config.state.origin;

export default class Upsell {
    constructor({ client, platform, sendBeacon = () => {} }) {
        let itsct = `Shazam_web_ext_${platform.deviceplatform}`;
        let ct = guid();

        Object.assign(this, {
            activateUrl: 'https://activate.apple.com/oauth2?',
            callback: origin + '/applemusic/signin/complete',
            client,
            activateParams: {
                client_id: 'xFmm2RMz',
                app: 'music',
                at: '1001l4DI',
                ct,
                mttnagencyid: 's2n', //AMP-11471 //'769459046716559743' --> WEB-756
                mttnsiteid: '125115',
                mttnsub1: itsct,
                mttnsub2: ct,
                mttn3pid: 'Apple-Shazam', //AMP-11471 //'a_custom_779816081798873874',
                itsct,
                itscg: '30201',
                clientbeaconuuid: ct,
            },
        });

        this._beacon = (beaconId, params) => {
            let manifestData = beaconsManifest[beaconId];
            let _dynamic = manifestData._dynamic || {};

            let beaconData = {
                ...manifestData,
                ...{
                    appname: this.client,
                    beaconType: manifestData.beaconType || 'userevent',
                },
            };
            delete beaconData._dynamic;

            Object.keys(_dynamic).forEach((key) => {
                let value = params[key];
                if (value) {
                    beaconData[key] = value;
                }
            });
            beaconData.section = beaconData.section || beaconData.screenname;
            sendBeacon(beaconData);
        };
        this.init();
    }
    init() {
        // coming from shazam.com signin complete flow postMessage
        window.addEventListener('message', (event) => {
            if (event.origin == origin) {
                if (event.data.action == 'signincomplete') {
                    this._onSigninComplete(event.data);
                } else if (event.data.action == 'activate') {
                    this.activate(event.data.params);
                }
            }
        });
    }
    getActivationState(cb) {
        chrome.storage.sync.get('amconnect', (result) => {
            cb(result.amconnect);
        });
    }
    getClientBeaconUuid() {
        return this.activateParams.clientbeaconuuid;
    }
    // o = { screenname, loginorigin}
    activate(o) {
        let params = this._verifyParams(Object.assign(o, this.activateParams));

        this._getCodeChallenge((data) => {
            if (data.error) {
                this._beacon('err-createcode--error', {
                    code: data.error.statusCode,
                    errordesc: data.error.message || 'unknown',
                    ...params,
                });
            } else {
                this._signin(params, data);
            }
        });
    }
    deactivate(cb = () => {}) {
        this.active = false;
        this._signinContext = null;

        chrome.storage.sync.get('amconnect', () => {
            chrome.storage.sync.remove('amconnect');

            chrome.tabs.query({}, (tabs) => {
                let shzTabs = tabs.filter((it) => it.url.search(/shazam/) !== -1);
                (shzTabs || []).forEach((tab) => {
                    chrome.tabs.sendMessage(tab.id, {
                        eventType: 'storage',
                        key: 'amconnect',
                        data: null,
                    });
                });
            });
            cb();
        });
    }

    getUpsellUrl(params) {
        const { enabled, data: flagData } = Upsell.featureflags.getFlag(`applemusic.signUpRedirectTo.campaign`);
        if (enabled) {
            let url = flagData.campaignURL;
            if (url.search(/^\//) !== -1) {
                url = `${origin}${url}`;
            }

            // TODO Question - Support playlist id , am playlist, an album?

            if (params.trackkey) {
                url += `/track/${params.trackkey}?referrer=browserextension`;
            }
            return url;
        }
    }
    gotoAppleMusicUpsell(amurl, params) {
        //let { screenname, loginorigin,  beaconId } = params;
        this.getActivationState((isActive) => {
            if (!isActive) {
                let url = this.getUpsellUrl(params);
                if (url) {
                    setTimeout(() => window.open(url, '_blank'), 200);
                } else {
                    this.activate(params);
                }
            } else {
                this.gotoAppleMusic(amurl, params);
            }
        });
    }

    gotoAppleMusic(amurl, params) {
        let { screenname, beaconId, trackkey } = params;
        let { ct, at, itsct, itscg, mttnagencyid, mttnsiteid, mttn3pid, app } = this.activateParams;

        //site i=1585723865&ign-itscg=30201&ign-itsct=Shazam_web&mttn3pid=Apple-Shazam&mttnagencyid=s2n&mttnsiteid=125115&mttnsub1=Shazam_web&mttnsub2=813e889a-7773-8d56-5d72-fb486b0963d0
        //exte i=1582660724&ign-itscg=30201&ign-itsct=Shazam_web_ext_desktop_mac&mttn3pid=a_custom_779816081798873874&mttnagencyid=769459046716559743&mttnsiteid=125115

        let queryParams = this._formQueryParams(amurl, {
            ct,
            at,
            itsct,
            itscg,
            mttnagencyid,
            mttnsiteid,
            mttn3pid,
            app,
            screenname,
            trackkey
        });
        // // go from object format to "a=1&b=2"
        let strparams = Object.keys(queryParams).map((key) => `${key}=${queryParams[key]}`);
        let url = amurl.replace(/\?[^#]*/, '?' + strparams.join('&'));

        this._beacon(beaconId, queryParams);
        //window.open implicitly closes extension popup, so need time for beacons to fire (or not???) ~ JP
        setTimeout(() => window.open(url, '_blank'), 200);
    }
    _signin(params, codedata) {
        let { codeVerifier, codeChallenge } = codedata;
        // custom beacon values : screenname_cluiid_appname_platform 
        let state = (params.state = `${params.screenname}_${params.clientbeaconuuid}_${this.client}_webextension`);

        let activateurl = this._getActivateUrl(params, codeChallenge);
        this._beacon('ue-connect--start', params);

        let handle = chrome.windows.create({
            // Just use the full URL if you need to open an external page
            url: activateurl,
            setSelfAsOpener: true,
            //alwaysOnTop: true,
            type: 'popup',
            left: window.screenX,
            top: window.screenY,
            height: 600,
            width: 500,
        });

        this._signinContext = { handle, state, params, codeVerifier };
    }
    // from shazam.com
    _onSigninComplete(data) {
        let { handle, state, params, codeVerifier } = this._signinContext;
        this._signupContext = null;

        handle && handle.close();
        // try {
        //     setTimeout(() => {
        //         handle && handle.close();
        //     }, 0); // set a timeout to close in case child couldn't close itself
        // } catch (exc) {
        //     console.log(exc);
        // }

        let error = (data.state != state || data.error) && {
            code: data.error || 'invalid_state',
            beaconId: data.error == 'access_denied' ? 'ue-connection--denied' : 'err-connect--error',
        };
        if (error) {
            this._activationComplete({ params, data, error });
        } else {
            this._beacon('ue-handshake--start', { ...params });
            
            this._exhangeCodeForToken({ code: data.code, codeVerifier }, (data) => {
                error = data.error && {
                    code: data.error,
                    beaconId: 'err-handshake--error',
                };
                this._activationComplete({ params, data, error });
            });
        }
    }

    _activationComplete({ params, data, error }) {
        let { clientbeaconuuid, loginorigin, screenname } = params;

        let event = {
            beaconId: 'ue-handshake--complete',
        };
        if (error) {
            event.beaconId = error.beaconId;
            event.providerdesc = error.code;
        }
        this._beacon(event.beaconId, { ...event, ...params });

        let mvargs = {
            screenname,
            clientbeaconuuid,
            loginorigin,
            appname: this.client,
            platform : 'webextension'
        };
        if (error) {
            mvargs.error = error.code || 'unknown';
        } else {
            mvargs.access_token = data.access_token;
            mvargs.isAccessAllowed = !!mvargs.access_token;
        }
        this._setConnectedState(mvargs.isAccessAllowed && data);
        
        if (mvargs.isAccessAllowed){
            this._beacon('ue-connection--success', { ...params });
        }

        let args = Object.keys(mvargs).map((key) => `${key}=${mvargs[key]}`);

        chrome.windows.create({
            // Just use the full URL if you need to open an external page
            url: origin + `/applemusic/signin/complete/confirm?${args.join('&')}`,
            setSelfAsOpener: true,
            type: 'popup',
            left: window.screenX,
            top: window.screenY,
            height: 375,
            width: 340,
        });
    }

    _setConnectedState(data) {
        let { access_token } = data || {};

        if (!access_token) {
            this.deactivate();
        } else {
            //dont need to synchronize with shazam.com, /signin/conplete/confirm will update window.storage
            chrome.storage.sync.set({ amconnect: data });
        }
    }
    _verifyParams(params) {
        [
            'app',
            'clientbeaconuuid',
            'ct',
            'at',
            'itsct',
            'itsct',
            'mttnagencyid',
            'mttnsiteid',
            'mttnsiteid',
            'mttnsub1',
            'mttnsub2',
            'screenname',
            // "state",
        ].forEach((key) => {
            if (!params[key]) {
                throw `Upsell:Missing parameter ${key}`;
            }
        });

        return params;
    }
    _getActivateUrl(params, codeChallenge) {
        //     mttnagencyid=769459046716559743&mttnsiteid=125115&mttn3pid=a_custom_779816081798873874&mttnsub1=Shazam_web_desktop_mac&mttnsub2=ee0b600d-b486-7416-3ff8-2bde074be189&ign-itsct=Shazam_web_desktop_mac&ign-itscg=30201
        let queryParams = Object.assign(
            {
                response_type: 'code',
                scope: 'music',
                redirect_uri: this.callback,
                code_challenge_method: 'S256',
                code_challenge: codeChallenge,
            },
            params
        );
        const queryString = Object.entries(queryParams)
            .map(([key, val]) => `${key}=${encodeURIComponent(val)}`)
            .join('&');

        let url = MOCKACTIVATE ? `${origin}/applemusic/signin/mockactivate?` : this.activateUrl;
        return url + queryString;
    }
    _getCodeChallenge(cb) {
        let codeVerifier = guid().replace(/-/g, '');

        fetch(`${origin}/services/partner/oauth/pkce/createcode`, {
            method: 'POST',
            body: JSON.stringify({
                code_verifier: codeVerifier,
            }),
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/json',
            },
        })
            .then((response) => response.json())
            .then((data = {}) => {
                cb(data.error ? data : { codeVerifier, codeChallenge: data.code_challenge });
            })
            .catch((error) => {
                cb({ error });
            });
    }
    _exhangeCodeForToken(params, cb) {
        if (MOCKACTIVATE && params.code == 'testcode') {
            cb({ access_token: 'testdat_token' });
            return;
        }

        let fetchParams = {
            grant_type: 'authorization_code',
            code: params.code,
            code_verifier: params.codeVerifier,
        };
        const postParams = Object.keys(fetchParams)
            .map((key) => {
                return key + '=' + fetchParams[key];
            })
            .join('&');

        fetch(`${origin}/services/partner/oauth/commerce/oauth2/token`, {
            method: 'POST',
            body: postParams,
            headers: {
                Accept: 'application/json',
                'Content-Type': 'application/x-www-form-urlencoded',
                'Cache-Control': 'no-cache',
                // NOTE: 'Authorization: Bearer {JWT_TOKEN}' gets added server side
            },
        })
            .then((response) => response.json())
            .then((data = {}) => {
                cb(data);
            })
            .catch((error) => {
                cb({ error });
            });
    }
    _formQueryParams(url, params) {
        // go from "a=1&b=2" format to object
        let strparams = url.match(/\?([^#]*)/); //?a=1&b=2
        strparams = (strparams && strparams[1]) || {}; // a=1&b=2
        let obj = {};
        strparams.split('&').reduce((obj, item) => {
            let parts = item.split('=');
            obj[parts[0]] = parts[1] || ''; //{ a : 1},{ b: 2}
            return obj;
        }, obj);

        return { ...obj, ...params }; // passed in take precedence

        // // go from object format to "a=1&b=2"
        // strparams = Object.keys(queryParams).map((key) => `${key}=${queryParams[key]}`);
        // return url.replace(/\?[^#]*/, '?' + strparams.join('&'));
    }
}

Upsell.featureflags = null; // safari doesnt support static

Config.getFeatureFlags((featureflags) => {
    Upsell.featureflags = featureflags;
});

