<template>
    <div>
        <div class="main-container" ref="main">
            <div class="shz-button-container" ref="buttonContainer">
                <svg @click="beginRecording(true)" id="logo-svg" viewBox="0 0 155.8 155.8" ref="logoSVG">
                    <g>
                        <path
                            fill-opacity=".3"
                            d="M77.9 15.6c-34.4 0-62.3 27.9-62.3 62.3s27.9 62.3 62.3 62.3 62.3-27.9 62.3-62.3-27.9-62.3-62.3-62.3z"
                        />
                        <path
                            d="M66.9 102.4c-6.6 0-13.3-2.2-18.4-6.6C42.3 90.3 38.6 83 38.1 75c-.5-7.7 2.2-15.2 7.4-21 5.9-6.5 16.5-16.6 16.9-17.1 2.9-2.7 7.4-2.6 10.1.2 2.7 2.9 2.6 7.4-.2 10.2-.1.1-10.6 10.2-16.2 16.3-2.6 2.9-3.9 6.6-3.7 10.5.3 4.1 2.2 7.9 5.5 10.8 4.4 3.8 13 4.6 18.3-.2 3.2-2.9 7.1-7.1 7.1-7.1 2.7-2.9 7.2-3.1 10.1-.4 2.9 2.7 3.1 7.2.4 10.2-.2.2-4.4 4.8-8.1 8.1-5 4.5-11.9 6.9-18.8 6.9z"
                        />
                        <path
                            d="M110.3 101.8c-5.9 6.5-16.5 16.6-16.9 17.1-1.4 1.3-3.2 2-5 2-1.9 0-3.8-.7-5.2-2.2-2.7-2.9-2.6-7.4.2-10.2.1-.1 10.6-10.2 16.2-16.3 2.6-2.9 3.9-6.6 3.7-10.5-.2-4.1-2.2-7.9-5.5-10.8-4.4-3.8-13-4.6-18.3.2-3.2 2.9-7.1 7.1-7.1 7.1-2.7 2.9-7.2 3.1-10.1.4-2.9-2.7-3.1-7.2-.4-10.2.2-.2 4.4-4.8 8.1-8.1 10.2-9.2 26.9-9.4 37.3-.4 6.3 5.4 10 12.8 10.5 20.8.4 7.9-2.2 15.3-7.5 21.1z"
                        />
                    </g>
                </svg>
            </div>
            <div v-if="listeningActive" class="listening-circle" id="c1"></div>
            <div v-if="listeningActive" class="listening-circle" id="c2"></div>
            <div v-if="listeningActive" class="listening-circle" id="c3"></div>
            <div v-if="listeningActive" class="listening-circle" id="c4"></div>
            <div class="text-container" ref="textContainer">
                <transition mode="out-in" name="mainText-appear">
                    <h2 class="click-text" :key="MAIN_TEXT">{{ MAIN_TEXT }}</h2>
                </transition>
                <transition mode="out-in" name="subText-appear">
                    <p class="click-subtext" :key="SUBTEXT">{{ SUBTEXT }}</p>
                </transition>
            </div>
        </div>
        <p v-if="listeningActive" class="cancel-text" @click="cancelListening">
            {{ CANCEL }}
        </p>
    </div>
</template>

<script>
export default {
    name: 'ShazamButton',
    mounted() {
        this.fetchStrings();
        this.$root.$on('listening_cancelled', () => {
            this.adjustFromListeningCard();
        });

        this.$nextTick(() => {
            this.$root.$on('retry_capture', () => {
                const _this = this;
                _this.stringIndex = 0;
                clearInterval(_this.textRotationInterval);
                _this.beginRecording(false);
            });
        });

        this.MAIN_TEXT = this.CLICK_TO;
        this.SUBTEXT = this.IDENTIFY_MUSIC;

        // start AUTOMATICALLY
        this.beginRecording(true);
    },
    methods: {
        beginRecording: function (mainClick) {
            const _this = this;
            this.stringIndex = 0;
            this.MAIN_TEXT = this.LISTENING_MAIN_STRINGS[this.stringIndex];
            this.SUBTEXT = this.LISTENING_SUB_STRINGS[this.stringIndex];
            this.listeningActive = true;
            this.initialTime = Date.now();
            browser.runtime.sendMessage({ type: 'startRecording' });

            const listeningScreenPageview = {
                beaconType: 'pageview',
                screenname: 'tagging',
            };

            const shazamButtonUserevent = {
                beaconType: 'userevent',
                type: 'taggingstarted',
                screenname: 'home',
            };

            if (mainClick) this.sendBeacon(shazamButtonUserevent);
            this.sendBeacon(listeningScreenPageview);

            this.adjustToListeningCard();

            // emit event to change popup contents
            this.$parent.$emit('recording_active');
            this.$root.$emit('audio_playing', null);

            _this.textRotationInterval = setInterval(() => {
                _this.stringIndex++;

                this.MAIN_TEXT = _this.LISTENING_MAIN_STRINGS[_this.stringIndex];
                _this.SUBTEXT = _this.LISTENING_SUB_STRINGS[_this.stringIndex];

                if (_this.stringIndex === 3) {
                    clearInterval(_this.textRotationInterval);
                }
            }, 3000);
            //
        },
        setActiveListeningText: function () {
            this.listeningActive = true;
            this.adjustToListeningCard();
        },
        cancelListening: function () {
            clearInterval(this.textRotationInterval);
            this.MAIN_TEXT = this.CLICK_TO;
            this.SUBTEXT = this.IDENTIFY_MUSIC;
            browser.runtime.sendMessage({ type: 'stopRecording' });

            const cancelButtonUserevent = {
                beaconType: 'userevent',
                type: 'taggingended',
                outcome: 'cancelled',
                timespent: Date.now() - this.initialTime,
            };
            this.sendBeacon(cancelButtonUserevent);
        },
        hidePopup: function () {
            const hideButtonUserevent = {
                beaconType: 'userevent',
                type: 'btn',
                providerdesc: 'hide',
            };
            this.sendBeacon(hideButtonUserevent);

            this.$root.$emit('close_popup');
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        adjustToListeningCard: function () {
            this.$refs.main.classList.add('main-container-active');
            this.$refs.buttonContainer.classList.add('button-active');
            this.$refs.textContainer.classList.add('text-container-active');
        },
        adjustFromListeningCard: function () {
            this.$refs.main.classList.remove('main-container-active');
            this.$refs.buttonContainer.classList.remove('button-active');
            this.$refs.textContainer.classList.remove('text-container-active');

            this.listeningActive = false;
            this.MAIN_TEXT = this.CLICK_TO;
            this.SUBTEXT = this.IDENTIFY_MUSIC;
            clearInterval(this.textRotationInterval);
        },
        fetchStrings: function () {
            this.LISTENING_MAIN_STRINGS[0] = browser.i18n.getMessage('shazambutton_listening_main1');
            this.LISTENING_MAIN_STRINGS[1] = browser.i18n.getMessage('shazambutton_listening_main2');
            this.LISTENING_MAIN_STRINGS[2] = browser.i18n.getMessage('shazambutton_listening_main3');
            this.LISTENING_MAIN_STRINGS[3] = browser.i18n.getMessage('shazambutton_listening_main4');
            this.LISTENING_SUB_STRINGS[0] = browser.i18n.getMessage('shazambutton_listening_sub1');
            this.LISTENING_SUB_STRINGS[1] = browser.i18n.getMessage('shazambutton_listening_sub2');
            this.LISTENING_SUB_STRINGS[2] = browser.i18n.getMessage('shazambutton_listening_sub3');
            this.LISTENING_SUB_STRINGS[3] = browser.i18n.getMessage('shazambutton_listening_sub4');
            this.HIDE = browser.i18n.getMessage('button_hide');
            this.CANCEL = browser.i18n.getMessage('button_cancel');
            this.CLICK_TO = browser.i18n.getMessage('shazambutton_click');
            this.IDENTIFY_MUSIC = browser.i18n.getMessage('shazambutton_subtext');
        },
    },
    data: function () {
        return {
            MAIN_TEXT: '',
            SUBTEXT: '',
            LISTENING_MAIN_STRINGS: ['', '', '', ''],
            LISTENING_SUB_STRINGS: ['', '', '', ''],
            HIDE: '',
            CANCEL: '',
            CLICK_TO: '',
            IDENTIFY_MUSIC: '',
            stringIndex: 0,
            listeningActive: false,
            textRotationInterval: '',
            initialTime: '',
        };
    },
};
</script>

