<template>
    <transition appear name="library">
        <a v-bind:href="content.weburl" target="content.weburl" @click="sendTrackBeacon">
            <div class="library-card" @mouseenter="handleHoverIn()" @mouseleave="handleHoverOut()">
                <cover-art-playback :content="content" size="small" />
                <div class="text-content">
                    <p id="title">{{ content.metadata.title }}</p>
                    <p id="artist">{{ content.metadata.artist }}</p>
                    <p v-if="(amConnected || (!localIsPlaying && !amConnected))" id="date">
                        {{ getDateTime(content.matchTime) }}
                    </p>
                    <div v-else class="marquee-container">
                        <p id="upsell-text">{{ UPSELL_TEXT }}</p>
                    </div>
                </div>

                <div @click="sendAppleMusicBeacon" class="am-button-container" v-if="hover">
                    <apple-music-button class="am-button" :amurl="content.metadata.openin" :trackId="content.trackId" />
                </div>
                <div class="rubbish-container" @click="deleteEntry" v-if="hover">
                    <svg id="rubbish-bin" xmlns="http://www.w3.org/2000/svg" width="14" height="16" viewBox="0 0 14 16">
                        <g fill="none" fill-rule="evenodd">
                            <g fill="#000" fill-rule="nonzero">
                                <g>
                                    <g>
                                        <path
                                            d="M376.394 17c.469 0 .835-.126 1.1-.377.265-.251.407-.613.427-1.085l.454-10.464h1.028c.166 0 .307-.057.423-.171.116-.114.174-.258.174-.43 0-.163-.058-.301-.174-.415-.116-.115-.257-.172-.423-.172h-3.138V2.797c0-.558-.17-.997-.51-1.317-.34-.32-.81-.48-1.41-.48h-2.713c-.595 0-1.062.16-1.403.48-.34.32-.51.759-.51 1.317v1.09h-3.13c-.16 0-.3.056-.415.17-.116.115-.174.253-.174.416 0 .172.058.316.174.43.116.114.254.171.416.171h1.035l.454 10.464c.02.472.164.834.43 1.085.268.251.633.377 1.097.377h6.788zm-1.52-13.114h-3.756v-1.02c0-.173.054-.31.162-.411.109-.102.256-.153.442-.153h2.54c.187 0 .336.051.446.153.111.101.167.238.167.41l-.001 1.021zm-4.157 10.966c-.136 0-.248-.04-.336-.121-.089-.082-.135-.193-.14-.335l-.204-7.738c-.005-.137.035-.247.12-.331.086-.084.202-.126.348-.126.131 0 .241.04.33.122.087.081.134.193.14.335l.21 7.738c.006.137-.035.247-.12.33-.086.085-.202.126-.348.126zm2.283 0c-.146 0-.263-.041-.352-.125-.088-.084-.132-.192-.132-.324V6.658c0-.137.044-.247.132-.331.089-.084.206-.126.352-.126.141 0 .257.042.348.126.09.084.136.194.136.331v7.745c0 .132-.046.24-.136.324-.091.084-.207.125-.348.125zm2.275 0c-.146 0-.262-.041-.347-.125-.086-.084-.129-.194-.129-.331l.22-7.738c.004-.142.051-.254.14-.335.087-.081.2-.122.336-.122.146 0 .262.042.347.126.086.084.126.194.121.331l-.219 7.745c0 .137-.044.246-.132.328-.088.08-.2.121-.337.121z"
                                            transform="translate(-404.000000, -196.000000) translate(18.000000, 30.000000) translate(20.000000, 165.000000)"
                                        />
                                    </g>
                                </g>
                            </g>
                        </g>
                    </svg>
                </div>
                <div class="new-container" v-else>
                    <div v-if="!dismissedNew && isNew">
                        <p class="new-text">NEW</p>
                    </div>
                </div>
            </div>
        </a>
    </transition>
</template>

<script>
import AppleMusicButton from './buttons/AppleMusicButton.vue';
import CoverArtPlayback from './CoverArtPlayback.vue';
import { format, formatDistanceToNow } from 'date-fns';
import { cs, de, el, enGB, enUS, es, fr, hi, id, it, ja, ko, nl, pl, pt, ru, tr, zhCN, zhTW } from 'date-fns/locale';
export default {
    name: 'LibraryTrack',
    components: { AppleMusicButton, CoverArtPlayback },
    props: {
        content: Object,
        isNew: String,
        amConnectionStatus: Boolean
    },
    mounted() {
        this.fetchStrings();

        this.$root.$on('audio_playing', (trackId, amConnected) => {
            this.amConnected = amConnected;
            if (trackId === this.content.trackId) {
                this.localIsPlaying = true;
            } else {
                this.localIsPlaying = false;
            }
        });

        this.$root.$on('audio_paused', (trackId) => {
            if (trackId === this.content.trackId) this.localIsPlaying = false;
        });
    },
    data() {
        return {
            hover: false,
            dismissedNew: false,
            locale: 'en',
            localIsPlaying: false,
            amConnected: false,
            UPSELL_TEXT: '',
            NEW: '',
            blockTrackBeacon: false
        };
    },
    computed: {
        defaultText() {
            return browser.i18n.getMessage('extName');
        },
    },
    methods: {
        getDateTime: function (time) {
            // if under 1hr return number of minutes
            // if under 24hrs return number of hours
            // otherwise return the date the match occured
            let dateString;
            const languageDictionary = {
                cs,
                de,
                el,
                en: enGB,
                'en-US': enUS,
                'en-GB': enGB,
                es,
                fr,
                hi,
                id,
                it,
                ja,
                ko,
                nl,
                pl,
                pt,
                ru,
                tr,
                zh: zhCN,
                'zh-CN': zhCN,
                'zh-TW': zhTW,
            };

            const _this = this;

            // neat little trick here to pull the lang from the system based on the
            // translated strings we have in _locales
            const translatedString = browser.i18n.getMessage('nomatch_no_audio');
            chrome.i18n.detectLanguage(translatedString, function(result) {
                if (result.isReliable && result.languages[0].percentage > 75) {
                    _this.locale = result.languages[0].language;
                }
            });

            const locale = languageDictionary[this.locale];
            const elapsedMS = Date.now() - new Date(time);

            const cutoff24hrs = 24 * 60 * 60 * 1000;

            if (elapsedMS <= cutoff24hrs) {
                dateString = formatDistanceToNow(new Date(time), {
                    addSuffix: true,
                    locale,
                });
            } else {
                dateString = format(new Date(time), 'PP • p', { locale });
            }

            return dateString;
        },
        deleteEntry: function (e) {
            e.preventDefault();
            const _this = this;
            const trackid = this.$props.content.trackId;
            chrome.storage.local.get('shazam_matches', function (result) {
                let currMatchArray = result['shazam_matches'];
                const deleteTarget = currMatchArray.findIndex((item) => item.trackId === trackid);
                currMatchArray.splice(deleteTarget, 1);
                chrome.storage.local.set({ shazam_matches: currMatchArray });

                _this.$parent.$emit('track_deleted', deleteTarget);
            });
        },
        sendTrackBeacon: function () {
            // potentially add providername: 'www.shazam.com'
            const historyTrackResultUserevent = {
                beaconType: 'userevent',
                type: 'nav',
                providerdesc: 'track-click',
                section: 'history',
                trackkey: this.content.trackId,
            };

            if (!this.blockTrackBeacon) {
                this.sendBeacon(historyTrackResultUserevent);
            } else {
                this.blockTrackBeacon = false;
            }
        },
        sendAppleMusicBeacon: function (e) {
            e.preventDefault();
            this.blockTrackBeacon = true;

            const goToWebFlowUserevent = {
                beaconType: 'userevent',
                type: 'open',
                screenname: 'amattribution',
                providername: 'applemusic',
                providerdesc: 'gotowebflow',
                section: 'amattribution',
                trackkey: this.content.trackId
            }

            // only need to send this webflow promo beacon while we have the
            // 5 month promo redirect occuring
            if (!this.amConnectionStatus) {
                this.sendBeacon(goToWebFlowUserevent);
            }
        },
        handleHoverIn: function () {
            this.hover = true;
            if (!this.dismissedNew) {
                this.dismissedNew = true;
                this.$root.$emit('update_new_matches', this.content.trackId);
            }
        },
        handleHoverOut: function () {
            this.hover = false;
        },
        fetchStrings: function () {
            this.UPSELL_TEXT = browser.i18n.getMessage('am_banner');
            this.NEW = browser.i18n.getMessage('library_new');
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
    },
};
</script>

