<template>
    <transition appear name="main-fadein">
        <div class="disconnect-card" ref="card">
            <div class="text-container">
                <h2 class="disconnect-headline">{{ DISCONNECT_MAIN }}</h2>
                <p class="disconnect-subtext">{{ DISCONNECT_SUB }}</p>
            </div>
            <div class="button-container">
                <button class="cancel-button" @click="cancel">{{ CANCEL }}</button>
                <button class="disconnect-button" @click="confirm">{{ DISCONNECT }}</button>
            </div>
        </div>
    </transition>
</template>

<script>
export default {
    name: 'DisconnectCard',
    data: function () {
        return {
            DISCONNECT_MAIN: '',
            DISCONNECT_SUB: '',
            DISCONNECT: '',
            CANCEL: '',
        };
    },
    props: {
        expanded: Boolean,
    },
    mounted() {
        this.fetchStrings();
    },
    methods: {
        cancel: function () {
            this.$root.$emit('toggle_disconnect_card', {
                toggle: false,
                expandedState: this.expanded,
            });
        },
        confirm: function () {
            this.$root.$emit('disconnect_apple_music');
        },
        fetchStrings: function () {
            this.DISCONNECT_MAIN = browser.i18n.getMessage('disconnect_main');
            this.DISCONNECT_SUB = browser.i18n.getMessage('disconnect_sub');
            this.DISCONNECT = browser.i18n.getMessage('am_button_disconnect');
            this.CANCEL = browser.i18n.getMessage('button_cancel');
        },
    },
};
</script>

