<template>
    <div class="rate-shazam-card" ref="card">
        <div class="text-container">
            <h2 class="rate-shazam-headline">{{ RATE_SHAZAM_MAIN }}</h2>
            <p class="rate-shazam-subtext">{{ RATE_SHAZAM_SUB }}</p>
        </div>
        <div class="button-container">
            <button class="cancel-button" @click="cancel">{{ CANCEL }}</button>
            <button class="rate-shazam-button" @click="confirm">{{ RATE }}</button>
        </div>
    </div>
</template>

<script>
export default {
    name: 'RateShazamCard',
    data: function () {
        return {
            RATE_SHAZAM_MAIN: '',
            RATE_SHAZAM_SUB: '',
            RATE: '',
            CANCEL: '',
            REVIEW_URL: 'https://chrome.google.com/webstore/detail/shazam-identify-songs-fro/mmioliijnhnoblpgimnlajmefafdfilb/reviews'
        };
    },
    mounted() {
        const ratingpagePageView = {
            beaconType: 'pageview',
            screenname: 'rating',
        }
        this.sendBeacon(ratingpagePageView);
        this.fetchStrings();
        this.updateRatingStatus();
    },
    methods: {
        cancel: function () {
           this.$parent.$emit('close_match', false);
            const cancelButtonUserevent = {
                beaconType: 'userevent',
                type: 'nav',
                providerdesc: 'notnow',
            };
            this.sendBeacon(cancelButtonUserevent);
        },
        confirm: function () {
            const beaconObject = {
                beaconType: 'userevent',
                type: 'nav',
                outcome: 'rate',
            };
            chrome.storage.local.set({ shazam_rating: {is_rated: 'yes'} });
            this.sendBeacon(beaconObject);
            setTimeout(() => {
                window.open(this.REVIEW_URL, '_blank');
                }, 200);
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        fetchStrings: function () {
            this.RATE_SHAZAM_MAIN = browser.i18n.getMessage('rate_shazam_main');
            this.RATE_SHAZAM_SUB = browser.i18n.getMessage('rate_shazam_sub');
            this.RATE = browser.i18n.getMessage('button_rate');
            this.CANCEL = browser.i18n.getMessage('button_not_now');
        },
        updateRatingStatus: function () {
            // update the ratingStatus to no once rating page is shown.
            chrome.storage.local.get('shazam_rating', function(result) {
                const ratingStatus = result.shazam_rating && result.shazam_rating.is_rated ? result.shazam_rating.is_rated : null;
                if(ratingStatus === 'pending')           
                {
                    chrome.storage.local.set({shazam_rating : {is_rated : 'no'}});         
                }
            });
        }
    }
};
</script>

