import * as FeatureFlags from '@shazam-web/featureflags';
import { getPlatformInfo } from '@shazam-web/useragent';

let state = {
    member: false,
    country: 'US',
    env: 'www', // env variable?
    origin: 'https://www.shazam.com', // env variable?
};

export default {
    state,
    getFeatureFlags(cb = () => {}) {
        if (this.featureflags) {
            cb(this.featureflags);
        }
        chrome.storage.local.get('country', function (result) {
            state.country = result.country || 'US';
            chrome.storage.local.get('inidDetails', function (result) {
                state.inid = (result && result.inidDetails && result.inidDetails.inid) || 'userinid'; //result.inidDetails.inid;
                chrome.storage.sync.get(['amconnect'], (result) => {
                    state.member = !!result.amconnect;
                    console.log(state);
                    if (!this.featureflags) {
                        getConfig((featureflags) => {
                            this.featureflags = featureflags;
                            featureflags.addCustomModules({
                                applemusic: {
                                    memberState(config) {
                                        let result = Object.keys(config).every((prop) => {
                                            return state[prop] === config[prop];
                                        });
                                        return result;
                                    },
                                },
                            });
                            cb(featureflags); //
                        });
                    }
                });
            });
        });
    },
};

function getConfig(cb) {
    FeatureFlags.configureFeatures(
        {
            url: `${state.origin}/config/features/website.json`,
        },
        {
            controlConfigs: {
                environments: ['www', 'cisd', 'betasd', 'dev'],
                getLoginState() {
                    return state.member;
                },
            },
            controlStates: {
                country: state.country,
                environment: state.env,
                inid: state.inid,
                platformProps: getPlatformInfo(navigator.userAgent),
                urlparams: {},
            },
        },
        ({ featureflags }) => {
            cb(featureflags);
        }
    );
}

chrome.storage.onChanged.addListener((changes, namespace) => {
    if (namespace == 'sync' && changes.amconnect) {
        state.member = !changes.newValue;
    }
});
