<script>
import Upsell from '../../features/upsell';
import Beacon from '../Beacon.vue';
export default {
    name: 'ConnectButton',
    props: {
        connected: Boolean,
        expandedState: Boolean,
    },
    mounted() {
        this.fetchStrings();
        this.setupUpsell();
        this.determineInitialState();
        //determine is an am connect token is present

        this.actionText = this.CONNECT;

        this.$root.$on('disconnect_apple_music', () => {
            // we make a bold assumption the deactivate always works
            this.upsell.deactivate(() => {
                this.$root.$emit('toggle_disconnect_card', {
                    toggle: false,
                    expandedState: false,
                });
                this.$root.$emit('send_beacon', {
                    beaconType: 'userevent',
                    type: 'streamingmusicflow',
                    action: 'disconnect',
                    screenname: 'webextension',
                    providername: 'applemusic',
                });

                // and to match the behaviour of connect, we close the popup
                // storage.onChanged event should handle this (with timeout to allow beacons to succeed)
                //window.close();
            });
        });
        // connect/disconnect called from a different process (shazam.com) - update state
        chrome.storage.onChanged.addListener((changes, namespace) => {
            if (namespace == 'sync' && changes.amconnect) {
                // sufficient to close window, reopen will refresh state
                // wait for beacons to fire ...
                this.$root.$emit('close_popup');
            }
        });
    },
    data: function () {
        return {
            actionText: '',
            isAMConnected: false,
            CONNECT: '',
            DISCONNECT: '',
        };
    },
    methods: {
        handleClick: function () {
            if (this.isAMConnected) {
                this.deactivate();
            } else {
                this.openUpsell();
            }
        },
        determineInitialState: function () {
            //chrome.storage.local.get('amconnect', (result) => {
            this.upsell.getActivationState((state) => {
                const amToken = state && state.access_token;
                this.isAMConnected = amToken ? true : false;

                if (this.isAMConnected) {
                    // this.$refs.pillBackground.classList.add('white-button');
                    // this.$refs.pillText.classList.add('black-text');
                    // this.$refs.connectText.classList.add('connect-text-inverted');
                    this.actionText = this.DISCONNECT;
                }
            });
        },
        openUpsell: function () {
            this.upsell.getActivationState((isActive) => {
                if (isActive) {
                    alert('already active');
                } else {
                    this.upsell.activate({
                        loginorigin: 'connect',
                        screenname: 'webextension',
                    });
                    const upsellStartUserEvent = {
                        beaconType: 'userevent',
                        type: 'open',
                        providerdesc: 'gotowebflow',
                        providername: 'applemusic',
                        screename: 'connect',
                        section: 'connect',
                        clientbeaconuuid: this.upsell.getClientBeaconUuid()
                    }
                    this.sendBeacon(upsellStartUserEvent);
                }
            });
        },
        deactivate: function () {
            this.upsell.getActivationState((isActive) => {
                if (!isActive) {
                    alert('already inactive');
                } else {
                    this.$root.$emit('toggle_disconnect_card', {
                        toggle: true,
                        expandedState: true,
                    });
                }
            });
        },
        setupUpsell: function () {
            this.upsell = new Upsell({
                client: 'browserextension',
                platform: Beacon.platform,
                sendBeacon: this.sendBeacon.bind(this),
            });
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        fetchStrings: function () {
            this.CONNECT = browser.i18n.getMessage('am_button_connect');
            this.DISCONNECT = browser.i18n.getMessage('am_button_disconnect');
        },
    },
};
</script>

