                                        d="M7.007 14c.247-.005.473-.057.68-.155.207-.098.412-.255.614-.472l5.27-5.657c.286-.299.429-.665.429-1.099 0-.294-.068-.563-.203-.808-.136-.245-.32-.441-.551-.588-.232-.147-.485-.221-.761-.221-.429 0-.806.178-1.131.534l-4.34 4.734-4.353-4.734C2.326 5.178 1.946 5 1.523 5c-.281 0-.536.074-.765.22-.23.148-.413.344-.551.589-.138.245-.207.514-.207.808 0 .217.036.414.107.592.072.178.179.347.322.507l5.277 5.657c.395.418.828.627 1.301.627z"
                                        transform="translate(-38.000000, -200.000000) translate(18.000000, 30.000000) translate(20.000000, 165.000000)"
                                    />
                                </g>
                            </g>
                        </g>
                    </g>
                </svg>

                <p class="shazams-text">{{ SHAZAMS }}</p>
                <transition appear name="text-appear">
                    <div v-show="newMatches.length > 0" class="new-pill">
                        <p :key="newMatches.length">{{ newMatches.length }}</p>
                    </div>
                </transition>
            </div>
            <connect-button :expandedState="expanded" v-show="!(isListening && isAMConnected)"/>
        </div>
        <div class="library-container" v-show="expanded">
            <div v-for="(track) in libraryContents" v-bind:key="track.trackId">
