                    viewBox="0 0 14 16"
                >
                    <g fill="none" fill-rule="evenodd">
                        <g fill="#d7374f" fill-rule="nonzero">
                            <g>
                                <g>
                                    <path
                                        d="M376.394 17c.469 0 .835-.126 1.1-.377.265-.251.407-.613.427-1.085l.454-10.464h1.028c.166 0 .307-.057.423-.171.116-.114.174-.258.174-.43 0-.163-.058-.301-.174-.415-.116-.115-.257-.172-.423-.172h-3.138V2.797c0-.558-.17-.997-.51-1.317-.34-.32-.81-.48-1.41-.48h-2.713c-.595 0-1.062.16-1.403.48-.34.32-.51.759-.51 1.317v1.09h-3.13c-.16 0-.3.056-.415.17-.116.115-.174.253-.174.416 0 .172.058.316.174.43.116.114.254.171.416.171h1.035l.454 10.464c.02.472.164.834.43 1.085.268.251.633.377 1.097.377h6.788zm-1.52-13.114h-3.756v-1.02c0-.173.054-.31.162-.411.109-.102.256-.153.442-.153h2.54c.187 0 .336.051.446.153.111.101.167.238.167.41l-.001 1.021zm-4.157 10.966c-.136 0-.248-.04-.336-.121-.089-.082-.135-.193-.14-.335l-.204-7.738c-.005-.137.035-.247.12-.331.086-.084.202-.126.348-.126.131 0 .241.04.33.122.087.081.134.193.14.335l.21 7.738c.006.137-.035.247-.12.33-.086.085-.202.126-.348.126zm2.283 0c-.146 0-.263-.041-.352-.125-.088-.084-.132-.192-.132-.324V6.658c0-.137.044-.247.132-.331.089-.084.206-.126.352-.126.141 0 .257.042.348.126.09.084.136.194.136.331v7.745c0 .132-.046.24-.136.324-.091.084-.207.125-.348.125zm2.275 0c-.146 0-.262-.041-.347-.125-.086-.084-.129-.194-.129-.331l.22-7.738c.004-.142.051-.254.14-.335.087-.081.2-.122.336-.122.146 0 .262.042.347.126.086.084.126.194.121.331l-.219 7.745c0 .137-.044.246-.132.328-.088.08-.2.121-.337.121z"
