</template>

<script>
import LibraryTrack from './LibraryTrack.vue';
import LibraryPlaceholder from './LibraryPlaceholder.vue';
import ConnectButton from './buttons/ConnectButton.vue';
import Upsell from '../features/upsell';
import Beacon from './Beacon.vue';
export default {
    components: { LibraryTrack, LibraryPlaceholder, ConnectButton },
    name: 'Library',
    data: function () {
        return {
            expanded: false,
            libraryContents: [],
            newMatches: [],
            SHAZAMS: '',
            NEW: '',
            NO_SHAZAMS_MAIN: '',
            NO_SHAZAMS_SUB: '',
            DELETE_ALL: '',
            audioPlaying: false,
            currentPlayingID: '',
            isAMConnected: false,
            showConnectButton: true,
            isListening: false,
        };
    },
    props: {
        musicKit: Object
    },
    mounted() {
        this.fetchLibrary();
        this.fetchNewMatches();
        this.fetchStrings();
        this.setupUpsell();
        this.musicKit;

        this.$on('track_deleted', (deleteTarget) => {
            this.libraryContents.splice(deleteTarget, 1);

            // refresh the library after the successful deletion
            this.fetchLibrary();
        });

        this.$root.$on('track_match', (updatedLibrary) => {
            this.libraryContents = updatedLibrary.reverse();
            this.fetchNewMatches();
        });

        this.$root.$on('update_new_count', () => {
            this.fetchNewMatches();
        });

        this.$root.$on('clear_library', () => {
            chrome.storage.local.set({ shazam_matches: [] });
            chrome.storage.local.set({ new_matches: [] });
            this.libraryContents = [];
            this.fetchLibrary();
            this.$parent.$emit('toggle_delete_card', {
                toggle: false,
                expandedState: false,
            });
        });

        this.$root.$on('audio_playing', (trackId) => {
            this.handlePlayingAudio(trackId);
        });

        this.$root.$on('audio_paused', () => {
            if (this.audioPlaying) {
                this.audioPlaying = false;
                this.currentPlayingID = '';
            }
        });

        this.$root.$on('audio_finished', () => {
            this.audioPlaying = false;
        });

        this.$root.$on('update_new_matches', (trackId) => {
            const currNewMatches = this.newMatches.filter(item => item !== trackId);
            chrome.storage.local.set({ new_matches: currNewMatches });
            this.fetchNewMatches();
        });

        this.$root.$on('listening_state_change', (state) => {
            this.isListening = state;
        });
    },
    methods: {
        toggleLibraryContent: function () {
            // send beacon if we're opening the library
            if (!this.expanded) {
                const libraryBeaconObject = {
                    beaconType: 'userevent',
                    type: 'btn',
                    providerdesc: 'history',
                    trackcount: this.libraryContents.length
                };
                this.sendBeacon(libraryBeaconObject);
            }

            this.expanded = !this.expanded;

            // flip the chevron direction
            this.$refs.chevron.classList.toggle('invert');

            // let the delete card keep track of the state
            this.$root.$emit('library_toggled', this.expanded);

            this.handlePlayingAudio();
        },
        forceCloseLibrary: function () {
            this.$refs.chevron.classList.remove('invert');
            this.expanded = false;
            this.$root.$emit('library_toggled', false);
        },
        fetchLibrary: function () {
            const _this = this;
            chrome.storage.local.get('shazam_matches', function (result) {
                if (result.shazam_matches) {
                    _this.libraryContents = result.shazam_matches.reverse();
                    // if there aren't any tracks disable the button
                    if (_this.libraryContents.length === 0 && _this.$refs.rubbish) {
                        _this.$refs.rubbish.classList.add('disable-rubbish');
                    }
                }
            });
        },
        clearLibrary: function (e) {
            e.preventDefault(); // stop clickthrough to trackpage
            this.$parent.$emit('toggle_delete_card', {
                toggle: true,
                expandedState: this.expanded,
            });
        },
        fetchNewMatches: function() {
            const _this = this;
            chrome.storage.local.get('new_matches', function (result) {
                _this.newMatches = result.new_matches || [];
            });
        },
        handlePlayingAudio: function (trackId) {
            if (this.audioPlaying) {
                this.$root.$emit('audio_controller_stop', this.currentPlayingID);
            }
            this.audioPlaying = true;
            this.currentPlayingID = trackId;
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        setupUpsell: function () {
            this.upsell = new Upsell({
                client: 'browserextension',
                platform: Beacon.platform,
                sendBeacon: this.sendBeacon.bind(this),
            });

            this.upsell.getActivationState((state) => {
                const amToken = state && state.access_token;
                this.isAMConnected = amToken ? true : false;
            });
        },
        fetchStrings: function () {
            this.SHAZAMS = browser.i18n.getMessage('general_shazams');
            this.NEW = browser.i18n.getMessage('library_new');
            this.NO_SHAZAMS_MAIN = browser.i18n.getMessage('library_no_shazams_main');
            this.NO_SHAZAMS_SUB = browser.i18n.getMessage('library_no_shazams_sub');
            this.DELETE_ALL = browser.i18n.getMessage('delete_all');
        },
    },
};
</script>

