<script>
import PlayFullSongButton from './buttons/PlayFullSongButton.vue';
import CoverArtPlayback from './CoverArtPlayback.vue';
import AppleMusicButton from './buttons/AppleMusicButton.vue';
import Upsell from '../features/upsell';
import Beacon from './Beacon.vue';
export default {
    name: 'MatchCard',
    components: { PlayFullSongButton, AppleMusicButton, CoverArtPlayback },
    props: {
        content: Object,
    },
    data: function () {
        return {
            SHAZAMS: '',
            DONE: '',
            AM_OFFER: '',
            showIntersitial: true,
            showCover: false,
            isAMConnected: false,
            blockTrackBeacon: false,
        };
    },
    mounted() {
        const _this = this;
        this.setupUpsell();
        this.fetchStrings();
        this.determineInitialState();

        const resultpagePageview = {
            beaconType: 'pageview',
            screenname: 'track',
            trackkey: this.content.trackId
        }
        this.sendBeacon(resultpagePageview);

        this.$refs.interstitialText.classList.add('interstitial-enter-anim');

        setTimeout(() => {
            _this.showIntersitial = false;
            _this.showCover = true;
            const { cover, matchMainText, amButtonTagCount } = _this.$refs;

            const coverArt = cover.$refs.coverContainer;

            coverArt.classList.add('match-cover-enter-anim');
            matchMainText.classList.add('match-text-enter-anim');
            amButtonTagCount.classList.add('match-fade-enter-anim');
        }, 1900);

        // need to shift the button to the right if connected
        if (this.isAMConnected) {
            this.$refs.amButtonTagCount.classList.add('slide-to-the-right');
        }
    },
    methods: {
        close: function (e) {
            e.preventDefault();
            this.lastSentBeacon = 'close'
            this.$parent.$emit('close_match', false);
            this.$root.$emit('update_new_count');

            const matchBackUserevent = {
                beaconType: 'userevent',
                type: 'nav',
                providerdesc: 'home',
            };
            this.blockTrackBeacon = true;
            this.sendBeacon(matchBackUserevent);
        },
        sendTrackBeacon: function () {
            const matchTrackResultUserevent = {
                beaconType: 'userevent',
                type: 'nav',
                providerdesc: 'track-click',
                section: 'main',
                trackkey: this.content.trackId,
            };
            if (!this.blockTrackBeacon) {
                this.sendBeacon(matchTrackResultUserevent);
            } else {
                this.blockTrackBeacon = false;
            }
        },
        sendAppleMusicBeacon: function () {
            const matchTrackAMUserevent = {
                beaconType: 'userevent',
                type: 'open',
                providername: 'applemusic',
                store: 'applemusic',
                section: 'main',
                trackkey: this.content.trackId,
            };
            this.sendBeacon(matchTrackAMUserevent);
        },
        handleAMTrackBeacon: function () {
            if (!this.isAMConnected) {
                const amTrackBeacon = {
                    beaconType: 'userevent',
                    type: 'open',
                    providername: 'applemusic',
                    providerdesc: 'gotowebflow',
                    section: 'trackupsell',
                    trackkey: this.content.trackId,
                }
                this.sendBeacon(amTrackBeacon);
            }
            this.blockTrackBeacon = true;
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        fetchStrings: function () {
            this.SHAZAMS = browser.i18n.getMessage('general_shazams');
            this.DONE = browser.i18n.getMessage('button_done');
            this.AM_OFFER = browser.i18n.getMessage('am_offer_3');
        },
        setupUpsell: function () {
            this.upsell = new Upsell({
                client: 'browserextension',
                platform: Beacon.platform,
                sendBeacon: this.sendBeacon.bind(this),
            });
        },
        determineInitialState: function () {
            //chrome.storage.local.get('amconnect', (result) => {
            this.upsell.getActivationState((state) => {
                const amToken = state && state.access_token;
                this.isAMConnected = amToken ? true : false;

                if (this.isAMConnected) {
                    this.$refs.pillBackground.classList.add('white-button');
                    this.$refs.pillText.classList.add('black-text');
                    this.$refs.connectText.classList.add('connect-text-inverted');
                }
            });
            // this.isAMConnected = true;
        },
    },
};
</script>

