<template>
    <div class="cover-container" ref="coverContainer">
        <img v-bind:src="content.metadata.coverart" ref="coverImage" />
        <div class="play-pause-container" @click="playAudio" ref="playPause" v-if="content.playbackURL && size != 'large'">
            <svg class="play-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 18 18" v-if="!localIsPlaying">
                <path
                    d="M16.558 6.769c1.972 1.232 1.974 3.228 0 4.462L6.57 17.474C4.598 18.706 3 17.824 3 15.488V2.512C3 .182 4.596-.708 6.57.526l9.988 6.243z"
                />
            </svg>

            <svg class="pause-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 20" v-else>
                <g>
                    <rect width="5.333" height="20" rx="2.667" />
                    <rect width="5.333" height="20" x="10.286" rx="2.667" />
                </g>
            </svg>
        </div>
        <div class="progress-circle" ref="progressCircleContainer">
            <svg height="50" width="50" ref="svg" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 50 50">
                <circle class="progress" cx="22" cy="22" r="21" ref="progressCircle" />
            </svg>
        </div>
    </div>
</template>

<script>
import {
    setQueueAndPlay,
    pause,
    stop,
    getPlaybackStatus,
    getCurrentPlaying,
    getMK,
    getCurrentTrackInfo,
} from '../features/musickit';
import Upsell from '../features/upsell';
import Beacon from './Beacon.vue';
export default {
    name: 'CoverArtPlayback',
    props: {
        content: Object,
        size: String,
    },
    data() {
        return {
            audioNode: '',
            localIsPlaying: false,
            musicKit: null,
            isAMConnected: false,
            mkListenerAdded: false,
            audioNodeListenerAdded: false,
            currentlyPlaying: null,
        };
    },
    mounted() {
        const _this = this;

        this.setupUpsell();
        this.determineInitialState();

        this.$root.$on('audio_controller_stop', (playing_trackId) => {
            if (playing_trackId === this.content.trackId) {
                if (this.isAMConnected) {
                    stop();
                } else {
                    // console.log('audio content stop...');
                    _this.audioNode.pause();
                    _this.audioNode.currentTime = 0;
                }
                _this.localIsPlaying = false;
                _this.handleProgressIndicator('ended', null);
            }
        });

        this.$root.$on('audio_playing', (playing_trackId) => {
            // remove the playing indicator from all other tracks
            // console.log('got audio_playing...', playing_trackId, this.content.trackId);

            // clear the progress indicators
            if (playing_trackId !== this.content.trackId) {
                this.$refs.progressCircle.classList.remove('progress-animate');
            }
        });

        this.$root.$on('audio_paused', (playing_trackId) => {
            // debugger;
            if (this.isAMConnected) {
                // full length playback uses adamIDs
                if (playing_trackId !== this.content.trackId) {
                    this.localIsPlaying = false;
                    this.handleProgressIndicator('ended', null);
                }
            } else {
                // while preview refers to trackIDs
                if (playing_trackId !== this.content.trackId) {
                    this.localIsPlaying = false;
                    this.removeProgressIndicator('ended');
                } else {
                    this.removeProgressIndicator('paused');
                }
            }
        });

        // mount the audio element
        _this.$nextTick(() => {
            const audio = new Audio(_this.content.playbackURL);
            _this.audioNode = audio;

            if (_this.size === 'large') {
                this.$refs.coverImage.classList.add('large-cover');
                // Note: We removed playback from the match screen, can add back if
                // ability is re-introduced ~ JP
                // this.$refs.playPause.classList.add('large-cover-play-pause');
            }
        });
    },
    methods: {
        playAudio: function (e) {
            e.preventDefault();
            e.stopPropagation();

            // console.log('am conncted?', this.isAMConnected);

            // only fire the beacon when going from pause to play
            if (!this.localIsPlaying) {
                const playerplayBeacon = {
                beaconType: 'userevent',
                type: 'playerplay',
                section: (this.size === 'large') ? 'main' : 'history',
                trackkey: this.content.trackId,
                };
                this.sendBeacon(playerplayBeacon);
            }

            // display the loading spinner
            if (this.content.adamId != this.currentlyPlaying) {
                const { progressCircleContainer, progressCircle } = this.$refs;
                progressCircle.classList.add('progress-dash-loading');
                progressCircle.classList.remove('progress-dash-removed');
                progressCircleContainer.classList.add('progress-circle-loading');
            }

            if (!this.isAMConnected) {
                const audioDuration = this.audioNode.duration;
                const durationString = `${Math.round(audioDuration * 1.855 * 1000)}ms`;

                // const progressCircle = this.$refs.progressCircle;
                const { progressCircleContainer, progressCircle } = this.$refs;

                if (!this.audioNodeListenerAdded) {
                    this.audioNode.addEventListener('play', () => {
                        progressCircle.classList.remove('progress-dash-loading');
                        progressCircleContainer.classList.remove('progress-circle-loading');
                    });

                    this.audioNode.addEventListener('ended', () => {
                        progressCircle.classList.remove('progress-animate');

                        // reset the playback time and change from pause to play button
                        this.localIsPlaying = false;
                        this.audioNode.currentTime = 0;
                        this.$root.$emit('audio_finished');
                    });
                }
                // debugger;

                if (this.audioNode.paused) {
                    this.audioNode.addEventListener('loadstart', () => {
                        // console.log('loading started...');
                    });

                    this.audioNode.play();
                    progressCircle.style.animationDuration = durationString;

                    // means it has been started but is paused
                    if (progressCircle.classList.contains('progress-animate')) {
                        progressCircle.style.animationPlayState = 'running';
                    } else {
                        progressCircle.classList.add('progress-animate');
                    }

                    // console.log(progressCircle.getAttribute('r'));

                    this.$root.$emit('audio_playing', this.content.trackId, this.isAMConnected);
                } else {
                    this.audioNode.pause();

                    progressCircle.style.animationPlayState = 'paused';
                    this.$root.$emit('audio_paused', this.content.trackId);
                }
            } else {
                // message listening from MusicKit
                const _this = this;
                this.musicKit = getMK();

                if (!this.mkListenerAdded) {
                    this.musicKit.addEventListener('playbackStateDidChange', (state) => {
                        _this.handleMKStateChanges(state);
                    });
                    this.mkListenerAdded = true;
                }

                const currentPlaying = getCurrentPlaying();
                const status = getPlaybackStatus();

                debugger

                if (currentPlaying === this.content.adamId) {
                    // console.log('pbstatus: ', status);
                    if (status === 0 || status === 3) {
                        setQueueAndPlay(this.content.adamId);
                    } else {
                        pause();
                    }
                } else {
                    setQueueAndPlay(this.content.adamId);

                    if (currentPlaying) {
                        this.$root.$emit('audio_paused', this.content.trackId);
                    }

                    this.$root.$emit('audio_playing', this.content.trackId, true);
                    // this.localIsPlaying = !this.localIsPlaying;
                }
            }

            this.localIsPlaying = !this.localIsPlaying;
        },
        removeProgressIndicator: function (state) {
            const { progressCircleContainer, progressCircle } = this.$refs;
            if (state === 'ended') {
                progressCircle.classList.add('progress-dash-removed');
                progressCircle.classList.remove('progress-animate');
                this.localIsPlaying = false;
            } else if (state === 'paused') {
                progressCircle.style.animationPlayState = 'paused';
                progressCircle.classList.remove('progress-dash-loading');
                progressCircleContainer.classList.remove('progress-circle-loading');
            }
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        setupUpsell: function () {
            this.upsell = new Upsell({
                client: 'browserextension',
                platform: Beacon.platform,
                sendBeacon: this.sendBeacon.bind(this),
            });
        },
        determineInitialState: function () {
            //chrome.storage.local.get('amconnect', (result) => {
            this.upsell.getActivationState((state) => {
                const amToken = state && state.access_token;
                this.isAMConnected = amToken ? true : false;
            });
        },
        handleProgressIndicator: function (action, duration) {
            // const progressCircle = this.$refs.progressCircle;
            // const progressCircleContainer = this.$refs.progressCircleContainer;
            const { progressCircleContainer, progressCircle } = this.$refs;
            if (action === 'start') {
                if (this.currentlyPlaying === this.content.adamId) {
                    const durationString = `${Math.round(duration * 1.854)}ms`;
                    progressCircle.style.animationDuration = durationString;
                    progressCircle.classList.remove('progress-dash-removed');
                    progressCircle.classList.add('progress-animate');
                }
            } else if (action === 'pause') {
                progressCircle.style.animationPlayState = 'paused';
                progressCircle.classList.remove('progress-dash-loading');
                progressCircleContainer.classList.remove('progress-circle-loading');
            } else if (action === 'ended') {
                progressCircle.classList.add('progress-dash-removed');
                progressCircle.classList.remove('progress-animate');
                this.localIsPlaying = false;
            } else if (action === 'resume') {
                progressCircle.style.animationPlayState = 'running';
            }
        },
        handleMKStateChanges: function (state) {
            // Playback states
            // 0: "none",
            // 1: "loading",
            // 2: "playing",
            // 3: "paused",
            // 4: "stopped",
            // 5: "ended",
            // 6: "seeking",
            // 8: "waiting",
            // 9: "stalled",
            // 10: "completed"

            // console.log(this.content.metadata.title);
            // console.log(state);
            // console.log('+++++++++++++++++++++++++');
            const from = state.oldState;
            const to = state.state;

            const { progressCircleContainer, progressCircle } = this.$refs;

            // if ((from === 0 && to === 1) || from === 1 || to === 8) {
            //     progressCircle.classList.add('progress-dash-loading');
            //     progressCircleContainer.classList.add('progress-circle-loading');
            // }

            if (from === 1 && to === 8) {
                // loading to waiting
                // debugger;
                // progressCircle.classList.add('progress-dash-loading');
                // progressCircleContainer.classList.add('progress-circle-loading');
                this.currentlyPlaying = getCurrentPlaying();
            } else if (from === 8 && to === 2) {
                // waiting to playing
                progressCircle.classList.remove('progress-dash-loading');
                progressCircleContainer.classList.remove('progress-circle-loading');

                const info = getCurrentTrackInfo();
                this.handleProgressIndicator('start', info.playbackDuration);
            } else if (from === 5 && to === 10) {
                this.handleProgressIndicator('ended', null);
            } else if (from === 2 && to === 3) {
                // playing to paused
                this.handleProgressIndicator('pause', null);
            } else if (from === 3 && to === 2) {
                // paused to playing
                this.handleProgressIndicator('resume', null);
            } else if (from === 8 && to === 8) {
                progressCircle.classList.add('progress-dash-loading');
                progressCircleContainer.classList.add('progress-circle-loading');
            }
        },
    },
};
</script>

<style scoped>
img {
    width: 70px;
    height: 70px;
    padding: 10px;
    border-radius: 15px;
}
.cover-container {
    position: relative;
}
.play-pause-container {
    position: absolute;
    top: 25px;
    left: 25px;
    height: 40px;
    width: 40px;
    background-color: rgba(0, 0, 0, 0.7);
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 2;
    cursor: pointer;
}
.progress-circle {
    position: absolute;
    top: 23px;
    left: 23px;
    height: 44px;
    width: 44px;
    transform: rotate(-90deg);
}
.progress {
    fill: transparent;
    stroke: white;
    stroke-width: 3;
    stroke-dasharray: 151px;
    stroke-dashoffset: 151px;
    stroke-linecap: round;
}
.progress-animate {
    animation: playIndicator;
}
.progress-dash-loading {
    stroke-dasharray: 151px;
    stroke-dashoffset: 121px;
}
.progress-circle-loading {
    animation: 1s loadingIndicator infinite;
}
.progress-dash-removed {
    animation-duration: 0ms;
    stroke-dashoffset: 151px;
}
.play-icon,
.pause-icon {
    fill: white;
    width: 14px;
    height: 14px;
}
.large-cover {
    width: 120px;
    height: 120px;
    border-radius: 6px;
    margin: 0px 15px;
    padding: 0px;
}
.large-cover-play-pause {
    top: 40px;
    left: 55px;
}
/* transitions */
.library-enter {
    opacity: 0;
}

.library-enter-active {
    transition: all 0.5s ease-out;
}

/* animations */
@keyframes playIndicator {
    100% {
        stroke-dashoffset: 0px;
        /* stroke: white; */
    }
}
@keyframes loadingIndicator {
    0% {
        transform: rotate(-90deg);
    }
    100% {
        transform: rotate(270deg);
    }
}
</style>
