<template>
    <div></div>
</template>

<script>
import { BeaconsUrl } from '@shazam-web/stats';
import { getPlatformInfo } from '@shazam-web/useragent';
export default {
    name: 'Beacon',
    data: function () {
        return {
            inid: '',
            country: '',
            language: '',
        };
    },
    platform: getPlatformInfo(navigator.userAgent, true),
    mounted() {
        // this can receive a beacon from app-wide
        this.fetchStorageItems();
        this.$root.$on('send_beacon', (beaconContent) => {
            // console.log('received a beacon...', beaconContent);

            // https://beacon.shazam.com/beacons/api/v1/beacon/website/1.0/en/US/web/-/beacon/303a731d-6ece-a2bf-1f90-e88fe54b2455/impression?screenname=home&providername=shazambutton&path=/webrec/homepage&ec=0&fc=0&webId=f16c1af0-1302-c078-0686-2e136ceb666e&inapp=0&bot=0&deviceplatform=desktop_mac&osver=10.15.7&buildnum=20210517-51b3dcab-149433&amc=0&aa=0&ea=0&ga=0&browsername=chrome&browserver=90.0.4430.93&ua=Mozilla/5.0_(Macintosh;_Intel_Mac_OS_X_10_15_7)_AppleWebKit/537.36_(KHTML,_like_Gecko)_Chrome/90.0.4430.93_Safari/537.36
            // const url = new BeaconsUrl('https://beacon.shazam.com/beacons/api/v1/beacon/{appname}/{appver}/{lang}/{country}/{platform}/{devicemodel}/beacon/{inid}/{beaconType}?',
            const useragent = navigator.userAgent;
            const ua = getPlatformInfo(useragent, true);
            const url = new BeaconsUrl(
                'https://beacon.shazam.com/beacons/api/v1/beacon/{appname}/{appver}/{lang}/{country}/webextension/-/beacon/{inid}/{beaconType}?',
                {
                    appname: 'browserextension',
                    appver: '0.0.1',
                    lang: this.language,
                    country: this.country,
                    inid: this.inid,
                }, // optional params for path
                {
                    defaultValue: '-',
                }
            );

            const deviceSpecs = {
                deviceplatform: ua.deviceplatform,
                browsername: ua.browserName,
                browserver: ua.browserVer,
            };

            const joinedBeaconContent = {
                ...deviceSpecs,
                ...beaconContent,
            };

            url.send(joinedBeaconContent, 'image');
        });
    },
    methods: {
        fetchStorageItems: function () {
            const _this = this;
            chrome.storage.local.get('country', function (result) {
                _this.country = result.country;
            });
            chrome.storage.local.get('language', function (result) {
                _this.language = result.language;
            });
            chrome.storage.local.get('inidDetails', function (result) {
                _this.inid = result.inidDetails.inid;

                // this only fires once on initial startup, we needed to do this due to
                // beacon initially not firing from PopupContent for some reason
                // it's bizzare that all "initial state" beacons need to be fired from here
                _this.$root.$emit('send_beacon', { beaconType: 'pageview', screenname: 'home'});
                _this.$root.$emit('send_beacon', { beaconType: 'pageview', screenname: 'tagging'});
                _this.$root.$emit('send_beacon', { beaconType: 'userevent', type: 'taggingstarted', screenname: 'home'});
            });
        },
    },
};
</script>
