<script>
import Upsell from '../../features/upsell';
import Beacon from '../Beacon.vue';
export default {
    name: 'AppleMusicButton',
    props: {
        amurl: Object,
        inverseColors: String,
        openIn: String,
        trackId: String,
    },
    data: function () {
        return {
            OPEN_IN: '',
        };
    },
    mounted() {
        this.setupUpsell();
        this.fetchStrings();
        if (this.inverseColors) {
            this.$refs.pillBackground.classList.add('whiteButton');
            this.$refs.pillText.classList.add('blackText');
        }

        if (this.openIn) {
            this.$refs.pillBackground.classList.add('flex-area');
        }
    },
    methods: {
        handleClick: function (e) {
            e.preventDefault();
            this.upsell.gotoAppleMusicUpsell(this.amurl.applemusic, {
                screenname: (this.openIn === 'true') ? 'main' : 'history',
                loginorigin: 'webextension',
                beaconId: 'ue-btn-open-in-am',
                trackkey: this.trackId,
            });
        },
        setupUpsell: function () {
            this.upsell = new Upsell({
                client: 'browserextension',
                platform: Beacon.platform,
                sendBeacon: this.sendBeacon.bind(this),
            });
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        fetchStrings: function () {
            this.OPEN_IN = browser.i18n.getMessage('am_button_openin');
        },
    },
};
</script>

