// check for access token before going down musickit path

/*global MusicKit*/
let mk;
let currentTrack = null;
let currTrackInfo = null;

async function setupMusicKit() {
    const tokenURL = 'https://www.shazam.com/services/partner/oauth/commerce/validate';
    const musickitTokenFetch = await fetch(tokenURL);
    const musickitDevToken = await musickitTokenFetch.headers.get('x-shz-validation');

    MusicKit.configure({
        developerToken: musickitDevToken,
        app: {
            name: 'Shazam.com',
            build: '0.0.1',
        },
    });

    mk = MusicKit.getInstance();

    chrome.storage.sync.get('amconnect', (result) => {
        if ('amconnect' in result) {
            const accessToken = result.amconnect['access_token'];
            mk.musicUserToken = accessToken;
        }
    });
}

function setQueueAndPlay(trackid) {
    // console.log('incoming', trackid);
    // console.log('current', currentTrack);

    mk.authorize()
        .then(() => {
            // we are changing tracks
            // could be from null to playing
            // or from trackId to another trackId
            if (trackid !== currentTrack) {
                mk.setQueue({ song: trackid }).then(() => {
                    currentTrack = trackid;
                    mk.player.play();

                    // return info on playback duration to setup indicator
                    const itemInfo = mk.player.queue.items[0];
                    currTrackInfo = itemInfo;
                });
            } else if (mk.player.playbackState === 3 && trackid === currentTrack) {
                mk.player.play();
            }
        })
        .catch((err) => {
            console.error(err);
        });
}

function pause() {
    mk.player.pause();
}

function stop() {
    mk.player.stop();
    currentTrack = null;
}

function getPlaybackStatus() {
    return mk.player.playbackState;
}

function getCurrentPlaying() {
    return currentTrack;
}

function getMK() {
    return mk;
}

function getCurrentTrackInfo() {
    return currTrackInfo;
}

export { setupMusicKit, setQueueAndPlay, pause, stop, getPlaybackStatus, getCurrentPlaying, getMK, getCurrentTrackInfo };
