<template>
    <div class="pill-body" @click="handleClick">
        <svg id="apple-music-note" width="35" height="35" viewBox="0 0 35 35">
            <defs>
                <linearGradient id="bmhy9m7zca" x1="50%" x2="50%" y1="0%" y2="100%">
                    <stop offset="0%" stop-color="#FB5C74" />
                    <stop offset="100%" stop-color="#FA233B" />
                </linearGradient>
            </defs>
            <g fill="none" fill-rule="evenodd">
                <g>
                    <g>
                        <g transform="translate(-90 -461) translate(85 456) translate(5 5)">
                            <circle
                                cx="17.5"
                                cy="17.5"
                                r="17.25"
                                fill="url(#bmhy9m7zca)"
                                stroke="#FFF"
                                stroke-opacity=".3"
                                stroke-width=".5"
                            />
                            <path
                                fill="#FFF"
                                d="M22.343 8.003c-.064.006-.626.107-.694.12L13.862 9.71h-.003c-.203.045-.362.117-.485.221-.149.126-.231.304-.262.51-.007.045-.018.134-.018.267v9.843c0 .23-.018.453-.172.644-.154.19-.345.247-.568.293l-.509.103c-.643.131-1.061.22-1.44.369-.363.142-.635.323-.85.551-.429.454-.604 1.069-.544 1.645.052.492.27.962.647 1.31.253.235.57.414.945.49.388.079.801.05 1.405-.072.321-.066.623-.168.91-.34.283-.168.527-.394.717-.669.19-.276.314-.582.381-.907.07-.336.087-.64.087-.975v-8.537c0-.458.129-.578.494-.667 0 0 6.473-1.319 6.774-1.378.422-.082.62.04.62.486v5.827c0 .231-.003.464-.158.656-.154.19-.345.248-.568.293l-.509.104c-.643.13-1.062.22-1.44.368-.363.142-.635.323-.85.552-.429.453-.619 1.068-.559 1.644.051.492.286.962.662 1.31.253.235.57.41.945.485.388.08.801.051 1.405-.072.321-.065.623-.163.91-.334.283-.169.527-.394.717-.67.19-.275.313-.581.381-.907.07-.336.073-.64.073-.975V8.698c0-.452-.236-.731-.657-.695"
