<template>
    <div>
        <div class='popup-container'>
            <shazam-button v-show="showButton" />
            <!-- <shazam-button v-show="cardContent === 'button'" /> -->
            <match-card :content="matchContent" v-if="cardContent === 'match'" />
            <no-match-card :wasSilent="noMatchSilence" v-if="cardContent === 'nomatch'" />
            <delete-card :expanded="libraryExpanded" v-if="cardContent === 'delete'" />
            <disconnect-card :expanded="libraryExpanded" v-if="cardContent === 'disconnect'" />
            <rate-shazam-card v-if="cardContent === 'rateshazam'" />
        </div>
        <library v-if="!hideLibrary" :musicKit="musicKit"/>
        <!-- <library :musicKit="musicKit" /> -->
        <beacon v-if="hasInid"/>
    </div>
</template>

<script>
import Library from './Library.vue';
import ShazamButton from './ShazamButton.vue';
import MatchCard from './MatchCard.vue';
import NoMatchCard from './NoMatchCard.vue';
import DeleteCard from './DeleteCard.vue';
import Beacon from './Beacon.vue';
import DisconnectCard from './DisconnectCard.vue';
import RateShazamCard from './RateShazamCard.vue';
import { setupMusicKit } from '../features/musickit';

const tinycolor = require('tinycolor2');

export default {
    components: {
        Library,
        ShazamButton,
        MatchCard,
        NoMatchCard,
        DeleteCard,
        Beacon,
        DisconnectCard,
        RateShazamCard
    },
    name: 'PopupContent',
    data: function () {
        return {
            cardContent: 'button',
            noMatchSilence: false,
            matchContent: {},
            hideLibrary: false,
            onMainCard: true,
            onMatchCard: false,
            libraryExpanded: false,
            timeoutID: '',
            appleMusicConnected: false,
            showButton: true,
            musicKit: null,
            hasInid: false,
        };
    },
    mounted() {
        const _this = this;
        setupMusicKit();

        chrome.storage.local.get(
            ('inidDetails',
            function (result) {
                const existingInid = result['inidDetails'];
                const inidExpiry = existingInid && existingInid.inidExpiry;
                const currTimeSec = Math.round(Date.now() / 1000);

                if (!existingInid || (currTimeSec > inidExpiry)) {
                    const inid = `${rs(8)}-${rs(4)}-${rs(4)}-${rs(4)}-${rs(12)}`;
                    
                    const oneYearSeconds = 365 * 24 * 60 * 60;
                    const inidExpiry = Math.round(Date.now() / 1000) + oneYearSeconds;
                    chrome.storage.local.set({ inidDetails: {inid, inidExpiry} });

                    chrome.storage.local.set({ shazam_matches: [] });
                    chrome.storage.local.set({ new_matches: [] });
                }
                _this.hasInid = true;
            })
        );

        function rs(length) {
            var result = '';
            const chars = '0123456789abcdef';
            for (var i = length; i > 0; --i) result += chars[Math.round(Math.random() * (chars.length - 1))];
            return result;
        }

        // parse the language from the browser
        const langaugeArray = navigator.languages;
        const currentLocale = langaugeArray[0];
        const langPartsArray = currentLocale.split('-');

        chrome.storage.local.set({ language: langPartsArray[0] });
        chrome.storage.local.set({ locale: currentLocale });
        
        chrome.storage.local.get(['country'], function(result) {
            if (!('country' in result)) {
                fetch('https://www.shazam.com/services/webrec/country', {
                mode: 'no-cors',
                }) //change this to www once webservices is deployed
                    .then((response) => response.json())
                    .then((data) => {
                    chrome.storage.local.set({ country: data.country });
                    })
                .catch(() => {
                    // if fetching the country fails for some reason just default to the US
                    chrome.storage.local.set({ country: 'US' });
                });
            }
        });


        // fetch and set the placeholder image when there is no cover art
        const noCoverURL = chrome.extension.getURL('assets/nocoverart.jpeg');
        chrome.storage.local.set({ no_cover_url: noCoverURL });
        // setup the listeners
        browser.runtime.onMessage.addListener(function (message) {
            let listeningEndUserevent = {};

            // diagnostic beacon which is fired when we receive minimal audio into
            // the audio buffer for whatever reason
            if (message.type === 'low_audio') {
                const lowBufferError = {
                    beaconType: 'taggingprogress',
                    outcome: 'error',
                    screenname: 'home'
                };
                
                _this.hideLibrary = false;
                _this.sendBeacon(lowBufferError);
            }

            if (message.type === 'trackmatch') {
                if (_this.timeoutID) clearTimeout(_this.timeoutID);
                _this.$root.$emit('track_match', message.library);
                _this.onMainCard = false;
                _this.libraryExpanded = false;

                const trackId = message.match.trackId;
                const discoverURL = `https://www.shazam.com/discovery/v5/en/US/web/-/track/${trackId}?shazamapiversion=v3`;
                fetch(discoverURL)
                    .then((response) => response.json())
                    .then((data) => {
                        const joecolor = data.images && data.images.joecolor;
                        const colors = joecolor && joecolor.match(/^b:(\w{6})p:(\w{6})s:(\w{6})t:(\w{6})q:(\w{6})$/);

                        let colorHex = '#000000';
                        if (joecolor) colorHex = `#${colors[1]}`;
                        message.match.bgColor = colorHex;

                        let hasLightBgColor = tinycolor(colorHex).isLight();
                        if (hasLightBgColor) {
                            message.match.bgColor = tinycolor(colorHex).darken(50).toString();
                        }

                        // update the matchContent prop with the newly acquired data
                        _this.matchContent = message.match;

                        // once we fetch the color we can transition over to the match card
                        _this.cardContent = 'match';
                        _this.showButton = false;
                        _this.onMatchCard = true;
                        _this.hideLibrary = false;
                    });

                listeningEndUserevent = {
                    beaconType: 'userevent',
                    type: 'taggingended',
                    outcome: 'match',
                    trackkey: message.match.trackId,
                };
                // match gets auto-hidden after 10s if no rating exprerience
                // otherwise, get auto-hidden after 5s aned show rating experience page
                let dismissTimer = message.rating ? 5000 : 10000;
                _this.timeoutID = setTimeout(() => {
                    if(message.rating)
                    {
                        _this.showRatingCard();
                    }
                    else {
                        _this.closeMatch();
                    }
                    _this.$root.$emit('update_new_count');
                }, dismissTimer);
            }
            else if (message.type === 'tracknomatch') {
                _this.cardContent = 'nomatch';
                _this.showButton = false;
                _this.noMatchSilence = false;
                _this.onMainCard = false;
                _this.hideLibrary = false;

                listeningEndUserevent = {
                    beaconType: 'userevent',
                    type: 'taggingended',
                    outcome: 'nomatch',
                };
            } else if (message.type === 'silence') {
                _this.cardContent = 'nomatch';
                _this.showButton = false;
                _this.noMatchSilence = true;
                _this.onMainCard = false;
                _this.hideLibrary = false;

                listeningEndUserevent = {
                    beaconType: 'userevent',
                    type: 'taggingended',
                    outcome: 'nomatch_noaudio',
                };
            } else if (message.type === 'cancelled') {
                _this.cardContent = 'button';
                _this.hideLibrary = false;

                const homepagePageView = {
                    beaconType: 'pageview',
                    screenname: 'home',
                };

                _this.sendBeacon(homepagePageView);

                //notify the button container to change the UI
                _this.$root.$emit('listening_cancelled');
            } else if (message.type === 'listeningState') {
                // handles the hiding of the connect button when listening is in progress
                _this.$root.$emit('listening_state_change', message.state);
            }
            if (listeningEndUserevent.beaconType) {
                _this.sendBeacon(listeningEndUserevent);
            }
        });

        this.$on('close_match', () => {
            this.closeMatch();
        });

        this.$on('recording_active', () => {
            _this.hideLibrary = false;
        });

        this.$on('close_match_restart', () => {
            _this.noMatchSilence = false;
            _this.onMainCard = true;
            _this.hideLibrary = false;
            _this.showButton = true;
            _this.cardContent = 'button';
            _this.$root.$emit('retry_capture');
        });

        this.$on('button_content', () => {
            _this.cardContent = 'button';
        });

        this.$on('toggle_delete_card', (state) => {
            const { toggle, expandedState } = state;
            _this.toggleCardContent('delete', toggle, expandedState);
        });

        this.$root.$on('toggle_disconnect_card', (state) => {
            const { toggle, expandedState } = state;
            _this.toggleCardContent('disconnect', toggle, expandedState);
            _this.$root.$emit('listening_cancelled');
            _this.onMainCard = true;
            _this.showButton = true;
        });
        this.$root.$on('close_popup', () => {
            // allow for beacons to fire before closing
            setTimeout(() => window.close(), 1000);
        });
    },
    computed: {
        defaultText() {
            return browser.i18n.getMessage('extName');
        },
    },
    methods: {
        toggleCardContent: function (origin, toggle, expandedState) {
            if (toggle) {
                this.libraryExpanded = expandedState;
                this.cardContent = origin;
                // this.hideLibrary = true;
            } else {
                this.cardContent = 'button';
                this.hideLibrary = false;
            }
        },
        sendStartMessage: function () {
            browser.runtime.sendMessage({ type: 'startRecording' });
        },
        sendBeacon: function (beaconObject) {
            this.$root.$emit('send_beacon', beaconObject);
        },
        showRatingCard: function()
        {
            this.cardContent = 'rateshazam';
            this.onMainCard = true;
            this.showButton = true;
            this.hideLibrary = false;
        },
        closeMatch: function () {
            this.$root.$emit('listening_cancelled');

            setTimeout(() => {
                this.cardContent = 'button';
                this.showButton = true;
                this.onMainCard = true;
                this.hideLibrary = false;
            }, 10);

            const homepagePageView = {
                beaconType: 'pageview',
                screenname: 'home',
            };

            this.sendBeacon(homepagePageView);
        },
    },
};
</script>

<style scoped>
p {
    font-size: 20px;
}
.popup-container {
    min-height: 200px;
}
</style>
